<!-->用户注册修改密码页面<-->
<template>
  <div id="userRegister">
    <!-- 三个选项栏 -->
    <div class="nav-outer">
      <li @click="currentTab=0" :class="{active:currentTab ==0}">
        <div class="nav-text clickable-nav-text">
          <i class="el-step__icon-inner el-icon-upload"></i>
          <span>用户注册</span></div>
        <div class="arrow"></div>
      </li>
      <li @click="currentTab=1" :class="{active:currentTab ==1}">
        <div class="nav-text clickable-nav-text">
          <i class="el-step__icon-inner el-icon-question"></i>
          <span>重置密码</span></div>
        <div class="arrow-next"></div>
      </li>
      <li style="flex-grow: 0;" :class="{active:currentTab ==2}">
        <div class="nav-text">
          <i class="fa fa-key" aria-hidden="true"></i>
          <span>修改密码</span></div>
      </li>
    </div>

    <!-- 主要内容 -->
    <div class="main-flex">
      <div class="inner-box" v-show="currentTab ==0">
        <!-- 注册 -->
        <el-form :model="ruleForm1" :rules="rules1" ref="ruleForm1" label-width="100px" class="demo-ruleForm">
          <el-form-item label="用户名" prop="username">
            <el-input v-model="ruleForm1.username"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input v-model="ruleForm1.password"></el-input>
          </el-form-item>
          <el-form-item label="邮箱" prop="email">
            <el-input v-model="ruleForm1.email"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button :type="checkCode1Type" :disabled="isDisabled1" @click="submitForm('ruleForm1',1)">{{checkCode1Text}}</el-button>
          </el-form-item>
          <el-form-item label="验证码" required>
            <el-input v-model="checkCode1"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="danger" :disabled='isDisabled2' @click="submitForm('ruleForm1',2)">注册</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="inner-box" v-show="currentTab ==1">
        <!-- 重置密码 -->
        <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
          <el-form-item label="邮箱" prop="email">
            <el-input v-model="ruleForm2.email"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button :type="checkCode2Type" :disabled="isDisabled3" @click="submitForm('ruleForm2',1)">{{checkCode2Text}}</el-button>
          </el-form-item>
          <el-form-item label="验证码" required>
            <el-input v-model="checkCode2"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="danger" :disabled='isDisabled4' @click="submitForm('ruleForm2',2)">提交</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="inner-box" v-show="currentTab ==2">
        <!-- 修改密码 -->
        <el-form :model="ruleForm3" :rules="rules3" ref="ruleForm3" label-width="100px" class="demo-ruleForm">
          <el-form-item label="邮箱" prop="email">
            <el-input v-model="ruleForm3.email"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="danger" @click="submitForm('ruleForm3')">提交</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import { userOption } from "@/common/registerOp.js";
import { userRegister, userValidate, userForget } from "@/api/sign.js";

export default {
  name: "userRegister",

  data() {
    return {
      currentPath: "first",
      currentTab: 0, //当前tab：0代表注册，1代表重置密码，2代表修改密码

      ruleForm1: {}, //注册表单
      rules1: {}, //注册规则
      checkCode1: "", //注册验证码
      checkCode1Text: "获取验证码", //注册验证码按钮文本；获取验证码/重新获取/获取完过60s可以再次获取
      checkCode1Type: "primary", //验证码按钮类型，每获取完一次60s内不可重复获取，变为黑色；类型为'info',
      isDisabled1: false,
      isDisabled2: true,

      ruleForm2: {}, //重置密码表单
      rules2: {}, //重置密码规则
      checkCode2: "", //重置密码验证码
      checkCode2Text: "获取验证码", //重置密码验证码按钮文本；获取验证码/重新获取/获取完过60s可以再次获取
      checkCode2Type: "primary", //验证码按钮类型，每获取完一次60s内不可重复获取，变为黑色；类型为'info',
      isDisabled3: false,
      isDisabled4: true,

      ruleForm3: {}, //修改密码表单
      rules3: {}, //修改密码规则

      queryMsg: this.$route.query
    };
  },
  methods: {
    init() {
      //显示路径
      let index = this.$router.options.routes[0].children.findIndex(
        menu => menu.showName == "个人中心"
      );
      global.currentPath.data = [index, 1];
    },
    submitForm(formName, num) {
      //传入两个参数，第一个为表格名称，ruleForm1代表注册，ruleForm2代表重置密码，ruleForm3代表修改密码；第二个参数为步骤，第一步为获取验证码，第二步为确认提交
      let that = this;
      if (formName == "ruleForm1") {
        //注册相关操作
        if (num == 1) {
          //注册时获取验证码
          let formData1 = new FormData();
          let data = that.ruleForm1,
            file;
          Object.keys(data).forEach(function(key) {
            formData1.append(key, data[key]);
          });
          that.$refs[formName].validate(valid => {
            if (valid) {
              let url = "/user/validate";
              userValidate(formData1, url)
                .then(res => {
                  if (res.result == "success") {
                    that.$message({
                      showClose: true,
                      message: "已发送验证码",
                      type: "success",
                      duration: 1000
                    });
                    that.isDisabled2 = false;
                    that.checkCode1Type = "info";
                    that.checkCode1Text = "(60秒)可重新发送";
                    that.isDisabled1 = true;
                    let num = 60;
                    let timer = window.setInterval(function() {
                      if (num > 0) {
                        --num;
                        that.checkCode1Text = "(" + num + "秒)可重新发送";
                      } else {
                        window.clearInterval(timer);
                        that.checkCode1Type = "primary";
                        that.checkCode1Text = "重新发送";
                        that.isDisabled1 = false;
                      }
                    }, 1000);
                  } else {
                    that.$message({
                      showClose: true,
                      message: res.message,
                      type: "error",
                      duration: 2000
                    });
                  }
                })
                .catch(err => {
                  that.$message({
                    showClose: true,
                    message: err.message,
                    type: "error",
                    duration: 2000
                  });
                });
            } else {
              that.$message({
                showClose: true,
                dangerouslyUseHTMLString: true,
                message: "<p>获取失败,请检查表单是否有误</p>",
                type: "error",
                duration: 2000
              });
            }
          });
        } else if (num == 2) {
          //注册时确认并提交验证码
          if (that.checkCode1) {
            let url = "/user/register";
            userRegister(that.checkCode1, url)
              .then(res => {
                if (res.result == "success") {
                  that.$refs[formName].resetFields();
                  that.$message({
                    showClose: true,
                    message: "注册成功",
                    type: "success",
                    duration: 2000
                  });
                } else {
                  that.$message({
                    showClose: true,
                    message: res.message,
                    type: "error",
                    duration: 2000
                  });
                }
              })
              .catch(err => {
                that.$message({
                  showClose: true,
                  message: err.message,
                  type: "error",
                  duration: 2000
                });
              });
          } else {
            that.$message({
              showClose: true,
              dangerouslyUseHTMLString: true,
              message: "<p>提交失败,验证码不能为空</p>",
              type: "error",
              duration: 2000
            });
          }
        }
      } else if (formName == "ruleForm2") {
        //重置密码相关操作
        if (num == 1) {
          //重置密码时获取验证码
          let formData2 = new FormData();
          let data = that.ruleForm2;
          Object.keys(data).forEach(function(key) {
            formData2.append(key, data[key]);
          });
          that.$refs[formName].validate(valid => {
            if (valid) {
              let url = "/user/forgetPassword";
              userValidate(formData2, url)
                .then(res => {
                  if (res.result == "success") {
                    that.$message({
                      showClose: true,
                      message: "已发送验证码",
                      type: "success",
                      duration: 1000
                    });
                    that.isDisabled4 = false;
                    that.checkCode2Type = "info";
                    that.checkCode2Text = "(60秒)可重新发送";
                    that.isDisabled3 = true;
                    let num = 60;
                    let timer = window.setInterval(function() {
                      if (num > 0) {
                        --num;
                        that.checkCode2Text = "(" + num + "秒)可重新发送";
                      } else {
                        window.clearInterval(timer);
                        that.checkCode2Type = "primary";
                        that.checkCode2Text = "重新发送";
                        that.isDisabled3 = false;
                      }
                    }, 1000);
                  } else {
                    that.$message({
                      showClose: true,
                      message: res.message,
                      type: "error",
                      duration: 2000
                    });
                  }
                })
                .catch(err => {
                  that.$message({
                    showClose: true,
                    message: err.message,
                    type: "error",
                    duration: 2000
                  });
                });
            } else {
              that.$message({
                showClose: true,
                dangerouslyUseHTMLString: true,
                message: "<p>获取失败,请检查表单是否有误</p>",
                type: "error",
                duration: 2000
              });
            }
          });
        } else if (num == 2) {
          //重置密码时确认并提交验证码
          if (that.checkCode2) {
            let url = "/user/validateEmail";
            userForget(that.checkCode2, url)
              .then(res => {
                if (res.result == "success") {
                  that.$refs[formName].resetFields();
                  that.$message({
                    showClose: true,
                    message: "验证成功",
                    type: "success",
                    duration: 2000
                  });
                  that.currentTab = 2;
                } else {
                  that.$message({
                    showClose: true,
                    message: res.message,
                    type: "error",
                    duration: 2000
                  });
                }
              })
              .catch(err => {
                that.$message({
                  showClose: true,
                  message: err.message,
                  type: "error",
                  duration: 2000
                });
              });
          } else {
            that.$message({
              showClose: true,
              dangerouslyUseHTMLString: true,
              message: "<p>提交失败,验证码不能为空</p>",
              type: "error",
              duration: 2000
            });
          }
        }
      } else if (formName == "ruleForm3") {
        //修改密码操作
        let formData3 = new FormData();
        let data = that.ruleForm3;
        Object.keys(data).forEach(function(key) {
          formData3.append(key, data[key]);
        });
        that.$refs[formName].validate(valid => {
          if (valid) {
            let url = "/user/resetPassword";
            userValidate(formData3, url)
              .then(res => {
                if ((res.message = "success")) {
                  that.$message({
                    showClose: true,
                    dangerouslyUseHTMLString: true,
                    message: "<p>修改成功</p>",
                    type: "error",
                    duration: 2000
                  });
                } else {
                  that.$message({
                    showClose: true,
                    message: res.message,
                    type: "error",
                    duration: 2000
                  });
                }
              })
              .catch(err => {
                that.$message({
                  showClose: true,
                  message: err.message,
                  type: "error",
                  duration: 2000
                });
              });
          } else {
            that.$message({
              showClose: true,
              dangerouslyUseHTMLString: true,
              message: "<p>获取失败,请检查表单是否有误</p>",
              type: "error",
              duration: 2000
            });
          }
        });
      }
    }
  },
  created() {
    this.ruleForm1 = userOption.firstForm;
    this.rules1 = userOption.firstRules;
    this.ruleForm2 = userOption.secondForm;
    this.rules2 = userOption.secondRules;
    this.ruleForm3 = userOption.thirdForm;
    this.rules3 = userOption.thirdRules;
  },
  activated() {
    this.init();
    this.queryMsg = this.$route.query;
    this.currentTab = this.queryMsg.type - 1;
  }
};
</script>


<style lang="scss" scoped>
$blue: #127de7;
#userRegister {
  & {
    height: 100%;
    width: 1200px;
    padding: 50px;
    margin: 10px auto;
    background: rgba(235, 240, 247, 0.932);
    border-radius: 2px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
  }
  .nav-outer {
    & {
      display: -webkit-flex;
      display: flex;
      -webkit-justify-content: space-between;
      justify-content: space-between;
      margin: 20px 0 50px 30px;
      padding: 13px 8%;
      border-radius: 4px;
      background: #f5f7fa;
    }
    li {
      & {
        font-weight: 500;
        font-size: 1rem;
        color: #86868f;
        display: flex;
        -webkit-box-align: stretch;
        -ms-flex-align: stretch;
        align-items: stretch;
        -webkit-box-flex: 1;
        -ms-flex-positive: 1;
        flex-grow: 1;
        cursor: default;
      }
      .clickable-nav-text:hover {
        color: $blue;
        cursor: pointer;
      }
      .arrow {
        & {
          position: relative;
          -webkit-box-flex: 1;
          -ms-flex-positive: 1;
          flex-grow: 1;
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-align: center;
          -ms-flex-align: center;
          align-items: center;
          -webkit-box-pack: center;
          -ms-flex-pack: center;
          justify-content: center;
        }
        &:after {
          content: "";
          display: inline-block;
          position: absolute;
          height: 18px;
          width: 1px;
          background: #717479;
        }
      }
      .arrow-next {
        & {
          -webkit-box-flex: 1;
          -ms-flex-positive: 1;
          flex-grow: 1;
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-align: center;
          -ms-flex-align: center;
          align-items: center;
          -webkit-box-pack: center;
          -ms-flex-pack: center;
          justify-content: center;
        }
        &:after {
          content: "";
          display: inline-block;
          position: absolute;
          height: 15px;
          width: 1px;
          background: #c0c4cc;
          -webkit-transform: rotate(45deg) translateY(4px);
          transform: rotate(45deg) translateY(4px);
          -webkit-transform-origin: 100% 100%;
          transform-origin: 100% 100%;
        }
        &:before {
          content: "";
          display: inline-block;
          position: absolute;
          height: 15px;
          width: 1px;
          background: #c0c4cc;
          -webkit-transform: rotate(-45deg) translateY(-4px);
          transform: rotate(-45deg) translateY(-4px);
          -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
        }
      }
    }
    li.active {
      font-weight: 600;
    }
  }
}
.steps {
  margin-left: 30px;
  margin: 20px 0 50px 30px;
}
.nav-text .el-step__icon-inner[class*="el-icon"]:not(.is-status) /deep/ {
  font-size: 1em;
}
</style>



